.app__about{
    flex: 1;
    width : 100%;
    flex-direction: column;
}

.app__profiles{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.app__profile-item{
    width : 220px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin : 2.5rem;

    img {
        width : 100%;
        height : 210px;
        object-fit: cover;
        border-radius : 15px;
    }
}